@import url(https://fonts.googleapis.com/css2?family=Jost:wght@200;300;400;500;600;700;800&family=Roboto:ital,wght@0,300;0,400;0,500;0,700;0,900;1,500;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  text-align: center;
}
@media screen and (max-width: 414px) {
  .bar_option {
    display: none;
  }
  .card {
    width: 90%;
  }
}
.header {
  width: 100%;
  height: 8vh;
  background-color: rgb(132, 96, 230);
}

.header h1 {
  color: #fff;
}

.covid-wrapper {
  display: flex;
}

.tables_wrapper {
  max-width: 100vw;
  display: flex;
  flex-direction: column;
  padding: 2%;
}

.table {
  margin-bottom: 20px;
  width: 400px;
}

.card {
  text-align: left;
  margin: 1%;
  border-radius: 15px;
  box-shadow: 0 10px 20px rgba(0,0,0,0.10), 0 6px 6px rgba(0,0,0,0.13);
  transition: .3s;
}

.card:hover {
  cursor: pointer;
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}

.flag {
  margin-left: 5px;
  width: 25px;
}
